import React from 'react';
import { CaseStudy, Challenge, TextContent, ImgContent } from '..';

import flow from './flow.jpg';
import legacy_logic from './legacy_logic.jpg';
import mock_1 from './mock_1.jpg';
import new_logic from './new_logic.jpg';
import progress from './progress.jpg';
import swim_lane from './swim_lane.jpg';

const problem = new Challenge(
    'Figure out the real problem',
    [
        new TextContent(
            <p>
                Although export / download data is a common feature can be found in many services, it is not saying we can simply copy one. Even facing the most familiar request ,We should not arbitrarily assume what we are truly dealing with.
            </p>
        ),
    ],
    [
        new TextContent(
            <React.Fragment>
                <p>
                    To figure out the full story, we traced out how data was produced and consumed in our system and through this we found the real problems waiting for solutions:
                </p>
                <ul>
                    <li>Relevant tech limitations</li>
                    <li>3 major yet very different use cases</li>
                </ul>
            </React.Fragment>
        ),
        new ImgContent(
            swim_lane,
            'Swim lane graph traces out how data lives in our system.'
        ),
    ]
);

const tech_limitaion = new Challenge(
    'Sometimes tech limitation is avoidable',
    [
        new TextContent(
            <p>
                To develop this feature we had strict limitation which was caused by the backend design.
            </p>
        ),
        new ImgContent(
            [legacy_logic],
            'The backend logic related to raw data file generating and downloading.'
        ),
        new TextContent(
            <React.Fragment>
                <p>
                    This design actually led to a bunch of problems:
                </p>
                <ul>
                    <li>Lack of the flexibility needed for well fulfilling different use cases.</li>
                    <li>Further lead to messy management and data cleaning for downloaded files.</li>
                    <li>Wasting server resource because of repetitively generating same data.</li>
                </ul>
            </React.Fragment>
        ),
    ],
    [
        new TextContent(
            <React.Fragment>
                <p>
                    We tried to dance within the limitation while seeking the reason for which the tech design was formed. Through discussion with peers we finally found the stories behind:
                </p>
                <ul>
                    <li>The product team did not notice the big trade off.</li>
                    <li>They believed instant starting download process was worth the highest priority.</li>
                    <li>Many big clients of the service downloaded raw data monthly.</li>
                </ul>
                <p>
                    In other words, <strong>this limitation came from the lack of research and problem understanding rather than a technical problem per se</strong>. Thus alternatives were proposed and discussed, and finally the tech limitation was solved with a new backend logic.
                </p>
            </React.Fragment>
        ),
        new ImgContent(
            [new_logic],
            'The new backend logic.'
        ),
        new TextContent(
            <React.Fragment>
                <p>
                    This new backend design also came with trade off: the downloading process can not start immediately any more, because it takes time to preparing the file. But it is much more acceptable with the following reasons and actions:
            </p>
                <ul>
                    <li>It won't take more than a few minutes.</li>
                    <li>We prestored the monthlong files to further optimize the useage for out big clients.</li>
                    <li>A progress modal was added to inform the users.</li>
                </ul>
            </React.Fragment>
        ),
        new ImgContent(
            [progress],
            'Wireframe of file generation progress.'
        ),
    ]
);

const auto = new Challenge(
    'Automate repetitive tasks',
    [
        new TextContent(
            <p>
                According to the research, one major use case is regularly fetching raw data for preparing report or integrating to other tools. This is an important yet boring and repetitive job for our users. It would be a good news if we could automate it.
            </p>
        ),
    ],
    [
        new TextContent(
            <React.Fragment>
                <p>
                    We introduced the Raw Data page a new feature: Subscription. A tip was also added in the ordinary download flow to help users notice this convenient tool.
                </p>
                <p>
                    Through this new feature our users now can config the receivers, schedule, data types and so on, to tell the service to send the data they need regularly yet automatically.
                </p>
            </React.Fragment>
        ),
        new ImgContent(
            mock_1,
            'Mockups of the primary data downloading and subscription pages.'
        ),
        new ImgContent(
            flow,
            'The flow how users could take advantage of the features provided by primary data page.'
        ),
    ]
);

export const RawData = new CaseStudy(
    [
        problem,
        tech_limitaion,
        auto,
    ]
);