import React from 'react';
/** @jsx jsx */
import { jsx } from '@emotion/core';

import { MediaQueryPoints, Space, Typeface, } from '../primitives';

export function ProjectInfo(props) {
    let infoCols = props.projectInfo.map((infoItem, i) =>
        <div key={i}>
            <div css={Typeface.label}>
                {infoItem.label}
            </div>
            <div css={Typeface.bodyS}>
                {infoItem.value}
            </div>
        </div>
    )
    return (
        <div css={styles.projectInfo}>
            {infoCols}
        </div>
    )
}

const styles = {
    projectInfo: {
        display: 'flex',
        flexWrap: 'wrap',
        label: 'ProjectInfo',
        marginBottom: -Space['16'],
        [MediaQueryPoints.mobile]: {
            flexDirection: 'column',
            flexWrap: 'noWrap',
        },
        '&>div': {
            marginBottom: Space['16'],
        },
        '&>:not(:last-child)': {
            marginRight: Space['120'],
            [MediaQueryPoints.mobile]: {
                marginRight: Space['64'],
            }
        },
    },
}