import React from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import { Page } from '../components';
import { ProjectPage, RootPage, ScrollToTop } from '.';
import { OrderedProjects } from '../contents';

export default function AppRouter() {
    let routes = OrderedProjects.map((project, i, projects) => (
        <Route
            key={i}
            path={project.project.link}
            render={props =>
                <ProjectPage
                    config={project.project}
                    next={i == projects.length - 1 ? null : projects[i + 1].project}
                    pre={i == 0 ? null : projects[i - 1].project}
                />}
        />
    ))
    return (
        <Router>
            <ScrollToTop>
                <Page>
                    <Route path="/" exact component={RootPage} />
                    {routes}
                </Page>
            </ScrollToTop>
        </Router>
    )
}