import React from 'react';
/** @jsx jsx */
import { jsx } from '@emotion/core';
import { Link, NavLink } from 'react-router-dom'

import { Color, Typeface } from '../primitives';
export { TinyNavLink, MidNavLink, MidLinkInvert, };

function TinyNavLink(props) {
    let { link, linkName, rejectRouter, } = props;
    return <MyNavLink link={link} linkName={linkName} rejectRouter={rejectRouter} style={Typeface.interactableTiny} />
}

function MidNavLink(props) {
    let { link, linkName, rejectRouter, } = props;
    return <MyNavLink link={link} linkName={linkName} rejectRouter={rejectRouter} style={Typeface.interactableMid} />
}

function MidLinkInvert(props) {
    let { link } = props;
    return <MyLink link={link} style={Typeface.interactableMidInvert} >{props.children}</MyLink>
}

function MyNavLink(props) {
    let { link, linkName, rejectRouter, style, } = props;
    return (
        rejectRouter ?
            <a css={style} href={link}>{linkName}</a> :
            <NavLink
                activeStyle={{
                    color: Color.black,
                    cursor: 'default',
                    textDecoration: 'none',
                }}
                css={style}
                exact
                to={link}
            >
                {linkName}
            </NavLink>
    )
}

function MyLink(props) {
    let { link, style } = props;
    return (
        <Link css={style} to={link}>{props.children}</Link>
    )
}