import React from 'react';
/** @jsx jsx */
import { jsx } from '@emotion/core';

import { Centric, } from '.';
import { Commentizer } from '../contents';
import { MediaQueryPoints, Space, Typeface, } from '../primitives';

export function StoryImg(props) {
    let { caption, src } = props;
    return (
        <div css={styles.storyImg}>
            {
                typeof src === 'string' ?
                    <img css={styles.img} src={src} alt={caption} /> :
                    <div css={styles.imgGroup}>
                        {
                            src.map((imgSrc, i) => (
                                <img key={i} src={imgSrc} alt={caption} />
                            ))
                        }
                    </div>
            }
            <Centric>
                <div css={styles.caption}>{Commentizer(caption)}</div>
            </Centric>
        </div>
    )
}

const styles = {
    storyImg: {
        label: 'StoryImg',
        // alignItems:'center',
        display: 'flex',
        flexDirection: 'column',
        position: 'relative',
        width: '100%',
        [MediaQueryPoints.big]: {
            maxWidth: 880,
        },
    },
    imgGroup: {
        label: 'ImgGroup',
        alignItems: 'center',
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'center',
        position: 'relative',
        marginTop: -Space['8'],
        [MediaQueryPoints.mobile]: {
            marginTop: -Space['4'],
            marginBottom: Space['4'],
        },
        '&>img': {
            // display: 'block',
            maxWidth: '100%',
            height: 'auto',
            margin: Space['8'],
            [MediaQueryPoints.mobile]: {
                margin: Space['4'],
            }
        }
    },
    img: {
        label: 'Img',
        marginBottom: Space['8'],
        width: '100%',
    },
    caption: [
        {
            label: 'Caption',
            textAlign: 'Center',
        },
        Typeface.comment.darkRegular,
    ]
}