import React from 'react';
/** @jsx jsx */
import { jsx } from '@emotion/core';

import { LeanLeft, } from '../components';
import { GeneralCM, } from '../contents';
import { BackgroundPattern, Color, MediaQueryPoints, Space, Typeface, } from '../primitives';

// about wrapper
//  LeanLeft
//      SelfSlogan
//          Yan---
//          slogan + enjoying queen
//      Abilities

export function Root_About(props) {
    return (
        <div css={[styles.about, BackgroundPattern]}>
            <LeanLeft>
                <SelfSlogan />
                <Abitities abilities={GeneralCM.abilities} />
            </LeanLeft>
        </div>
    )
}

const SelfSlogan = props => (
    <div css={styles.selfSlogan}>
        <h1 css={Typeface.displayLarge}>
            <div css={styles.firstLineWrapper}>
                Yan
                <div css={styles.line}></div>
            </div>
            <div>
                <span css={{ color: Color.primary }}>{GeneralCM.selfSlogan.selfDefinition}</span>
                {GeneralCM.selfSlogan.doing}
                <span css={Typeface.displayLargeWeak}>{GeneralCM.selfSlogan.accent}</span>
                .
            </div>
        </h1>
    </div>
)

const Abitities = props => {
    let abilities = props.abilities.map((ability, i, array) =>
        <React.Fragment>
            <div key={i}>
                {ability.category}
                <Keywords keywords={ability.keywords} />
            </div>
            {i == (array.length - 1) ? null : <div key={i + 100}>x</div>}
        </React.Fragment>
    );

    return (
        <div css={[styles.abilities, Typeface.displayS]}>
            {abilities}
        </div>
    )
}

const Keywords = props => {
    let keywords = props.keywords.map((keyword, i) =>
        <div key={i} css={{ opacity: 1 - i * 0.125 }}>{keyword}</div>
    )
    return (
        <div css={[styles.keywords, Typeface.bodyList, Typeface.reset.toBase]}>
            {keywords}
        </div>
    )
}



const styles = {
    about: {
        label: 'Section_About',
        position: 'relative',
    },
    selfSlogan: {
        label: 'SelfSlogan',
        alignItems: 'center',
        display: 'flex',
        // style for fill-vh block
        minHeight: '100vh',
        paddingTop: Space['120'],
        paddingBottom: Space['120'],
    },
    firstLineWrapper: {
        label: 'FirstLine',
        alignItems: 'baseline',
        display: 'flex',
    },
    line: {
        label: 'HorizonLine',
        backgroundColor: Color.light,
        flex: '1 1 auto',
        height: '28px',
        marginLeft: Space['16'],
        marginRight: Space['16'],
        [MediaQueryPoints.mobile]: {
            marginLeft: Space['8'],
            marginRight: Space['8'],
            height: '16px',
        },
    },
    abilities: {
        label: 'Abilities',
        display: 'flex',
        justifyContent: 'space-between',
        minHeight: '50vh',
        paddingBottom: Space['120'],
        width: '100%',
        [MediaQueryPoints.mobile]: {
            flexDirection: 'column',
        },
    },
    keywords: {
        label: 'Keywords',
        display: 'flex',
        flexDirection: 'column',
        overflow: 'visible',
        width: 1,
        whiteSpace: 'nowrap',
        [MediaQueryPoints.mobile]: {
            flexDirection: 'row',
            flexWrap:'wrap',
            width: 'auto',
            '&>:not(:last-child)': {
                '&::after':{
                    content:`" • "`,
                    whiteSpace:'pre',
                }
            },
        },
    },
}