export { CaseStudy, Challenge, TextContent, ImgContent, EmbedContent };

class CaseStudy {
    constructor(challenges) {
        this.challenges = challenges;
    }
}

class Challenge {
    constructor(title, problem, solution) {
        this.title = title;
        this.problem = problem;
        this.solution = solution;
    }
}

class TextContent {
    constructor(content) {
        this.type = 'text';
        this.content = content;
    }
}

class ImgContent {
    constructor(src, caption) {
        this.type = 'img';
        this.src = src;
        this.caption = caption;
    }
}

class EmbedContent {
    constructor(code, caption) {
        this.type = 'embed';
        this.code = code;
        this.caption = caption;
    }
}