import React from 'react';
/** @jsx jsx */
import { jsx } from '@emotion/core';

import { MoreStories, ProjectCard, } from '../components';
import { OrderedProjects } from '../contents';
import { Color } from '../primitives';

export function Projects(props) {
    let projectCards = OrderedProjects.map((project, i) => (
        <ProjectCard key={i} config={project.project} col={project.col} />
    ))
    return (
        <div css={style}>
            {projectCards}
            <MoreStories/>
        </div>
    )
}

const style = {
    label: 'Projects',
    display: 'flex',
    flexWrap: 'wrap',
    width: '100%',
}