import React from 'react';
/** @jsx jsx */
import { jsx } from '@emotion/core';
import { MediaQueryPoints } from '../primitives';

export function HeroImg(props) {
    return (
        <div css={[style, { backgroundImage: `url("${props.heroImg}")` }]}></div>
    )
}

const style = {
    label: 'HeroImage',
    backgroundAttachment: 'fixed',
    backgroundPosition: 'center',
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'cover',
    height: '100vh',
    width: '100%,',
    [MediaQueryPoints.mobile]: {
        backgroundAttachment: 'scroll',
    }
}