import React from 'react';
/** @jsx jsx */
import { jsx } from '@emotion/core';
import { Link } from 'react-router-dom'

import { ColumnWrapper, } from '.';
import { Commentizer, } from '../contents';
import { Color, MediaQueryPoints, Space, Typeface } from '../primitives'

// wrapper
// -card
// -text wrapper
// --tag
// --title (h2)
// --brief
export { ProjectCard, ProjectCardsWrapper };

const ProjectCard = (props) => {
    let { bg, bgIsDark, brief, color, link, mascot, tags, title, } = props.config;
    let col = props.col;
    return (
        <ColumnWrapper col={col}>
            <Link to={link}>
                <div
                    to={link}
                    css={[
                        styles.cardWrapper,
                        { backgroundImage: `linear-gradient(${bg})` },
                        { '&::after': { backgroundColor: color } },
                    ]}
                    onClick={void(0)}
                >
                    <div css={styles.mascotWrapper}>
                        <img css={[styles.mascotImg, { transform: `translateX(${mascot.transX}%)` }]} src={mascot.src} alt={title} />
                    </div>
                    <div css={[
                        styles.textWrapper,
                        bgIsDark ? { color: 'white' } : { color: Color.black },
                    ]}>
                        <div css={Typeface.comment.bold}>{Commentizer(tags)}</div>
                        <h2 css={Typeface.displayMid}>{title}</h2>
                        <p css={Typeface.body}>{brief}</p>
                    </div>
                </div>
            </Link>
        </ColumnWrapper>
    )
}

const ProjectCardsWrapper = (props) => (
    <div css={styles.cardsWrapper}>
        {props.children}
    </div>
)

const styles = {
    cardsWrapper: {
        label: 'ProjectCards',
        backgroundColor: Color.dark,
        display: 'flex',
        flexWrap: 'wrap',
        width: '100%',
    },
    cardWrapper: {
        height: 540,
        label: 'CardWrapper',
        overflow: 'hidden',
        position: 'relative',
        width: '100%',
        '&::after': {
            backgroundColor: 'black',
            content: `''`,
            display: 'block',
            height: '100%',
            left: 0,
            opacity: 0,
            position: 'absolute',
            top: 0,
            transition: `all 0.24s`,
            width: '100%',
        },
        '&:hover': {
            cursor: 'pointer',
            '&::after': {
                opacity: 0.72,
                transition: `all 0.24s`,
            },
        },
        [MediaQueryPoints.mobile]: {
            height: 460,
        }
    },
    mascotWrapper: {
        label: 'Mascot',
        position: 'absolute',
        height: '100%',
        top: 0,
        right: '12.5%',
    },
    mascotImg: {
        height: '100%',
    },
    textWrapper: {
        display: 'flex',
        flexDirection: 'column',
        height: '100%',
        justifyContent: 'flex-end',
        label: 'TextWrapper',
        paddingBottom: Space['40'],
        marginLeft: Space['40'],
        position: 'relative',
        width: 256,
        zIndex: 1000,
        [MediaQueryPoints.mobile]: {
            paddingBottom: Space['24'],
            marginLeft: Space['16'],
        }
    },
}