import React from 'react';
/** @jsx jsx */
import { jsx } from '@emotion/core';
import { Link } from 'react-router-dom';

import { TinyNavLink } from '.';
// import { Commentizer, } from '../contents';
import { MediaQueryPoints, Space, Typeface, } from '../primitives';

export function TopNav(props) {
    const navItems = props.navItems.map((item, i) =>
        <NavItem key={i} comment={item.comment} link={item.link} linkName={item.linkName} rejectRouter={item.rejectRouter ? true : false} />
    );
    return (
        <nav css={styles.topNav}>
            <Logo />
            <div css={styles.items}>
                {navItems}
            </div>
        </nav>
    )
}
