export function CaseStudy(props){
    let content = props.content.challenges.map((challenge, i) =>
        <div key={i} css={styles.challenge}>
            <h2 css={styles.challengeTitle}>{challenge.title}</h2>
            {Stories(challenge.problem)}
            {
                challenge.solution?(
                    <React.Fragment>
                        <Centric>
                            <h3 css={Typeface.bodyComment}>// Solution</h3>
                        </Centric>
                        {Stories(challenge.solution)}
                    </React.Fragment>):
                null
            }    
        </div>
    )
    return (
        <div css={styles.caseStudy}>
                {content}
        </div>
    )
}

function Stories(pieces) {
    let stories = pieces.map((storyPiece, i) => {
        if (storyPiece.type == 'text') {
            return storyPiece.content
        }
        else if (storyPiece.type == 'img') {
            return <StoryImg key={i} caption={storyPiece.caption} src={storyPiece.src}/>;
        }
        else if (storyPiece.type == 'embed') {
            return <RespEmbedVideo caption={storyPiece.caption} embedCode={storyPiece.code}></RespEmbedVideo>;
        }

    })
    return stories;
}

