class ProjectRole {
    constructor(role) {
        this.label = 'Role';
        this.value = role;
    }
}

class ProjectClient {
    constructor(client) {
        this.label = 'Client';
        this.value = client;
    }
}

class Project {
    constructor(bg, bgIsDark, brief, caseStudy, client, color, heroImg, link, mascotSrc, mascotTransX, role, story, tags, title, ) {
        this.bg = bg;
        this.bgIsDark = bgIsDark;
        this.brief = brief;
        this.caseStudy = caseStudy;
        this.color = color;
        this.heroImg = heroImg;
        this.link = `/project${link}`;
        this.mascot = {
            src: mascotSrc,
            transX: mascotTransX,
        };
        this.projectInfo = [new ProjectRole(role), new ProjectClient(client)];
        this.story = story;
        this.tags = tags;
        this.title = title;
    }
}

class OrderedProject {
    constructor(project, col) {
        this.project = project;
        this.col = col;
    }
}

